@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap);
* {
  box-sizing: border-box;
}

body {
  font-family: "Nunito Sans", sans-serif;
  margin: 0;
  background-color: hsl(207, 26%, 17%);
  max-width: 100%;
}

body.light {
  background-color: hsl(0, 0%, 98%);
}

.container {
  margin: 0 auto;
  padding: 50px;
}

.header {
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 70px;
  justify-content: space-between;
  background-color: hsl(209, 23%, 22%);
  color: hsl(0, 0%, 100%);
}

.header.light {
  background-color: hsl(0, 0%, 100%);
  color: hsl(200, 15%, 8%);
}

.header i {
  margin-right: 20px;
}

button {
  background-color: inherit;
  color: hsl(0, 0%, 100%);
  border: none;
  cursor: pointer;
  font-size: 16px;
}

button.light {
  color: hsl(200, 15%, 8%);
}

.fa.light {
  color: hsl(200, 15%, 8%);
}

.filter form {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.filter form > div {
  flex: 1 1;
}

.filter form .input {
  position: relative;
}

.filter form .input {
  position: relative;
}

input {
  width: 50%;
  padding: 20px 60px 20px 20px;
  display: block;
  background-color: hsl(209, 23%, 22%);
  color: hsl(0, 0%, 100%);
  text-indent: 40px;
  border: none;
  font-size: 14px;
  box-shadow: 0px 0px 10px rgba(27, 27, 27, 0.1);
}

input.light {
  background-color: hsl(0, 0%, 100%);
  color: hsl(200, 15%, 8%);
}

input::-webkit-input-placeholder {
  color: hsl(0, 0%, 100%);
  font-size: 14px;
}

input:-ms-input-placeholder {
  color: hsl(0, 0%, 100%);
  font-size: 14px;
}

input::placeholder {
  color: hsl(0, 0%, 100%);
  font-size: 14px;
}
input.light::-webkit-input-placeholder {
  color: hsl(200, 15%, 8%);
}
input.light:-ms-input-placeholder {
  color: hsl(200, 15%, 8%);
}
input.light::placeholder {
  color: hsl(200, 15%, 8%);
}

.input button {
  color: hsl(0, 0%, 100%);
  background: transparent;
  border: none;
  cursor: pointer;
  display: inline-block;
  font-size: 20px;
  position: absolute;
  top: 0;
  left: 0;
  padding: 18px 20px;
  z-index: 2;
}

.input.ligit button {
  color: hsl(200, 15%, 8%);
}

